import { SET_LANGUAGE } from '../constant'

const initState = {
    lexicon: {
        nav: {
            home: '',
            tracklist: '',
            settings: ''
        },
        home: {
            newproducts: '',
            bestsellers: '',
            direct: '',
            track: '',
            tracked: '',
            search: '',
            searchAlert: '',
            notFound: ''
        },
        track: {
            title: '',
            cover: '',
            name: '',
            price: '',
            action: '',
            twd: '',
            direct: '',
            delete: '',
            deleteAlert: '?',
            notTrack: '',
            totalNumber: '',
            totalAmount: '',
            clearTracked: '',
            clearConfirm: '?',
            checkAlert: ''
        },
        settings: {
            title: '',
            language: '',            
            languageFamily: 'zh',
        }
    },
}

export default function bestdealReducer(preState=initState,action){
    const {type,data} = action
    switch (type) {
        case SET_LANGUAGE:
            return {...preState, lexicon: {...data}}
        default:
            return preState
    }
}